import React ,{useState}from 'react'

export default function Calculator(props) {
  const [value, setvalue] = useState('');

  // const [mstyle, setmstyle] = useState('')

  

  const valuechange = (event)=>{
    setvalue(event.target.value);

  }
 
  const target = (num)=>{
    setvalue(value +num )

  }
  const targetfinal= ()=>{
    try{
      const acvalue = eval(value);
      setvalue(acvalue);
      props.showalert('Procesing......', 'success');


    }
    catch(error){
      setvalue(error);

    }
   
  }

  const targetAC = ()=>{
    setvalue('');
    props.showalert('All text clear', 'success');

  }

  // let mstyle = ()=>{

  //   if(props.mode=='dark'){
  //     setmstyle({
  //       // backgroundColor:'green'
  //     })

  //   }
  // }
  



return (
  
   <div className='container my-5  ' style={{backgroundColor:props.mode==='dark'?'#0c2233':'white',    color:props.mode==='dark'?'white':'black'}} >
          <hr />
         
            <div  className="mb-3 my-30 mx-10">
              <label htmlFor="exampleFormControlTextarea1" className="form-label mx-2 my-5"  style={{backgroundColor:props.mode==='dark'?'#0c2233':'white'}}> <h2>---Calculator--- </h2></label>
              <textarea  placeholder='Enter value here:' value={value} onChange={valuechange} className="form-control  " id="exampleFormControlTextarea1" style={{backgroundColor:props.mode==='dark'?'#0c2233':'white',border:'2px solid white', color:props.mode==='dark'?'white':'black'}}   rows="1"></textarea>
            </div>

            <div className="container">
              {/* <button onClick={target.bind(null, 9)} type="button" className="btn btn-light">=</button>
              <button onClick={target2} type="button" className="btn btn-light">2</button>
              <button onClick={target3} type="button" className="btn btn-light">+</button> */}
                  <button onClick={targetAC} type="button" className="btn btn-info mx-2 my-1" style={{ width: 55 }}>AC</button>
                  <button onClick={target.bind(null, 7)} type="button" className="btn btn-info mx-2 my-1" style={{ width: 55 }}>**</button>
                  <button onClick={target.bind(null, 7)} type="button" className="btn btn-info mx-2 my-1" style={{ width: 55 }}>+/-</button>
                  <button onClick={target.bind(null, '/')} type="button" className="btn btn-info mx-2 my-1" style={{ width: 55 }}>/</button>

            </div>

            <div className="container">
              <button onClick={target.bind(null, 7)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>7</button>
              <button onClick={target.bind(null, 8)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>8</button>
              <button onClick={target.bind(null, 9)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>9</button>
              <button onClick={target.bind(null, '*')} type="button" className="btn btn-info mx-2 my-1" style={{ width: 55 }}>*</button>
            </div>

            <div className="container">
              <button onClick={target.bind(null, 4)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>4</button>
              <button onClick={target.bind(null, 5)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>5</button>
              <button onClick={target.bind(null, 6)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>6</button>
              <button onClick={target.bind(null, '-')} type="button" className="btn btn-info mx-2 my-1" style={{ width: 55 }}>-</button>
            </div>

            <div className="container">
              <button onClick={target.bind(null, 1)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>1</button>
              <button onClick={target.bind(null, 2)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>2</button>
              <button onClick={target.bind(null, 3)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>3</button>
              <button onClick={target.bind(null, '+')} type="button" className="btn btn-info mx-2 my-1" style={{ width: 55 }}>+</button>
            </div>

            <div className="container">
              <button onClick={target.bind(null, '/')} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>/</button>
              <button onClick={target.bind(null, 0)} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>0</button>
              <button onClick={target.bind(null, '.')} type="button" className="btn btn-light mx-2 my-1" style={{ width: 55 }}>.</button>
              <button onClick={targetfinal} type="button" className="btn btn-success mx-2 my-1" style={{ width: 55 }}>=</button>
            </div>
</div>










      

    
  )
}
